#shf-settings {
	overflow: hidden;
	
	.form-table {
		margin-top: 0;

		tr {
			position: relative;
			margin-bottom: 20px;
			display: block;
			max-width: 100%;
			border: 1px solid #e5e5e5;
			-moz-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
			-webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
			box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
			-moz-box-sizing: border-box;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
		}

		th,
		td {
			padding: 12px 15px;
			background: #FFF;
			display: block;
			width: 100%;
			-moz-box-sizing: border-box;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
			margin-bottom: 0;
		}

		td {
			padding-top: 0;

			textarea {
				width: 100%;
				font-family: monospace;
			}
		}

		input[type="checkbox"] {
			margin-top: 1px;
			margin-right: 0px;
		}
	
	}

	.shf-postbox {
		select {
			margin: 10px 0 15px 0;
		}

		input[type="file"] {
			max-width: 250px;
		}

		.hndle {
			cursor: inherit;
		}
	}

	.shf-hook-priority {
		margin-top: 10px;
		margin-left: 10px;
		background: none repeat scroll 0 0 #f1f1f1;
		display: inline-block;
		padding: 4px 10px;
		font-size: 14px;
		vertical-align: middle;

		input {
			width: 40px;
			text-align: center;
		}
	}

	.shf-hook-disable {
		margin-top: 10px;
		background: none repeat scroll 0 0 #f1f1f1;
		display: inline-block;
		padding: 8px 10px;
		font-size: 14px;
		vertical-align: middle;
	}

	.CodeMirror {
		padding: 0;
		line-height: 1.4;
		font-family: monospace;
		font-size: 14px;
		height: 200px;
		border: 1px solid #ddd;
		-webkit-box-shadow: inset 0 1px 2px rgba( 0, 0, 0, 0.07 );
		box-shadow: inset 0 1px 2px rgba( 0, 0, 0, 0.07 );
	}

}